    <div class="zez">
      <h1>正则大全</h1>
      <div class="zdiv">
        <ul style="display: flex; justify-content: space-between;">
          <li style="width: 25%; height: 100%;">
            <h1>一、校验数字的js正则表达式</h1>
            <ul class="listuls">
              <li>1 数字：^[0-9]*$</li>
              <li>2 n位的数字：^d{n}$</li>
              <li>3 至少n位的数字：^d{n,}$</li>
              <li>4 m-n位的数字：^d{m,n}$</li>
              <li>5 零和非零开头的数字：^(0|[1-9][0-9]*)$</li>
              <li>6 非零开头的最多带两位小数的数字：^([1-9][0-9]*)+(.[0-9]{1,2})?$</li>
              <li>7 带1-2位小数的正数或负数：^(-)?d+(.d{1,2})?$</li>
              <li>8 正数、负数、和小数：^(-|+)?d+(.d+)?$</li>
              <li>9 有两位小数的正实数：^[0-9]+(.[0-9]{2})?$</li>
              <li>10 有1~3位小数的正实数：^[0-9]+(.[0-9]{1,3})?$</li>
              <li>11 非零的正整数：^[1-9]d*$ 或 ^([1-9][0-9]*){1,3}$ 或 ^+?[1-9][0-9]*$</li>
              <li>12 非零的负整数：^-[1-9][]0-9"*$ 或 ^-[1-9]d*$</li>
              <li>13 非负整数：^d+$ 或 ^[1-9]d*|0$</li>
              <li>14 非正浮点数：^((-d+(.d+)?)|(0+(.0+)?))$</li>
            </ul>
          </li>
          <li style="width: 25%; height: 100%;">
            <h1>二、校验字符的js正则表达式</h1>
            <ul class="listuls">
              <li>1 汉字：^[u4e00-u9fa5]{0,}$</li>
              <li>2 英文和数字：^[A-Za-z0-9]+$ 或 ^[A-Za-z0-9]{4,40}$</li>
              <li>3 长度为3-20的所有字符：^.{3,20}$</li>
              <li>4 由26个英文字母组成的字符串：^[A-Za-z]+$</li>
              <li>5 由26个大写英文字母组成的字符串：^[A-Z]+$</li>
              <li>6 由26个小写英文字母组成的字符串：^[a-z]+$</li>
              <li>7 由数字和26个英文字母组成的字符串：^[A-Za-z0-9]+$</li>
              <li>8 由数字、26个英文字母或者下划线组成的字符串：^w+$ 或 ^w{3,20}$</li>
              <li>9 中文、英文、数字包括下划线：^[u4E00-u9FA5A-Za-z0-9_]+$</li>
              <li>10 中文、英文、数字但不包括下划线等符号：^[u4E00-u9FA5A-Za-z0-9]+$</li>
              <li>11 可以输入含有^%&',;=?$"等字符：[^%&',;=?$x22]+</li>
              <li>12 禁止输入含有~的字符：[^~x22]+</li>
            </ul>
          </li>
          <li style="width: 25%; height: 100%;">
            <h1>三、特殊需求js正则表达式</h1>
            <ul class="listuls">
              <li>1 Email邮箱地址：^w+([-+.]w+)*@w+([-.]w+)*.w+([-.]w+)*$另外一个： /^[A-Za-z0-9._%-]+@([A-Za-z0-9-]+.)+[A-Za-z]{2,4}$/</li>
              <li>2 url域名：[a-zA-Z0-9][-a-zA-Z0-9]{0,62}(/.[a-zA-Z0-9][-a-zA-Z0-9]{0,62})+/.?</li>
              <li>3 InternetURL：[a-zA-z]+://[^s]* 或 ^http://([w-]+.)+[w-]+(/[w-./?%&=]*)?$</li>
              <li>4 手机号码：^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])d{8}$ 另外一个：/^1[3|4|5|7|8]d{9}$/</li>
              <li>5 电话号码：^((d{3,4}-)|d{3.4}-)?d{7,8}$</li>
              <li>6 国内电话号码(0511-4405222、021-87888822)：d{3}-d{8}|d{4}-d{7}</li>

              <li>12 日期格式：^d{4}-d{1,2}-d{1,2}</li>
              <li>13 一年的12个月(01～09和1～12)：^(0?[1-9]|1[0-2])$</li>
              <li>14 一个月的31天(01～09和1～31)：^((0?[1-9])|((1|2)[0-9])|30|31)$</li>
            </ul>
          </li>
          <li style="width: 25%; height: 100%;">
            <h1>四、特殊需求</h1>
            <ul class="listuls">
              <li>7 身份证号：15或18位身份证：^d{15}|d{18}$   15位身份证：^[1-9]d{7}((0d)|(1[0-2]))(([0|1|2]d)|3[0-1])d{3}$
                　　　　　　18位身份证：^[1-9]d{5}[1-9]d{3}((0d)|(1[0-2]))(([0|1|2]d)|3[0-1])d{4}$ 　8 短身份证号码(数字、字母x结尾)：^([0-9]){7,18}(x|X)?$ 或 ^d{8,18}|[0-9x]{8,18}|[0-9X]{8,18}?$</li>
              <li>9 帐号是否合法(字母开头，允许5-16字节，允许字母数字下划线)：^[a-zA-Z][a-zA-Z0-9_]{4,15}$</li>
              <li>10 密码(以字母开头，长度在6~18之间，只能包含字母、数字和下划线)：^[a-zA-Z]w{5,17}$</li>
              <li>11 强密码(必须包含大小写字母和数字的组合，不能使用特殊字符，长度在8-10之间)：^(?=.*d)(?=.*[a-z])(?=.*[A-Z]).{8,10}$</li>
            </ul>
          </li>
        </ul>
