<script>
export default {
  data() {
    var isPrice = (rule, value, callback) => {
      if (!value) {
        callback();
      } else {
        var reg = /^-?\d{1,16}(?:\.\d{1,4})?$/;

        if (reg.test(value)) {
          callback();
        } else {
          callback(new Error('数字格式:1-16位或小数点后可加1到4位'));
        }
      }
    };
    var youxiang = (rule, value, callback) => {
      if (!value) {
        callback();
      } else {
        var reg =
          /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (reg.test(value)) {
          console.log(1);
          callback();
        } else {
          callback(new Error('请输入正确的邮箱格式'));
        }
      }
    };
    var shouji = (rule, value, callback) => {
      if (!value) {
        callback();
      } else {
        var reg =
          /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
        if (reg.test(value)) {
          callback();
        } else {
          callback(new Error('请输入正确的手机好'));
        }
      }
    };
    var sfz = (rule, value, callback) => {
      if (!value) {
        callback();
      } else {
        var reg =
          /^([1-6][1-9]|50)\d{4}(18|19|20)\d{2}((0[1-9])|10|11|12)(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
        if (reg.test(value)) {
          callback();
        } else {
          callback(new Error('请输入正确的身份证号'));
        }
      }
    };
    return {
      // 弹窗
      pudelog: false,
      // form表单的填写内容
      ruleForm: {
        name: '',
        region: '',
        desc: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        region: [{ required: true, message: '请输入区域', trigger: 'change' }],
        desc: [{ required: true, message: '请填写内容', trigger: 'blur' }]
      },
      rules2: {
        name: [
          {
            required: true,
            message: '请输入数值型 格式为00000000.0000 / 0000000',
            trigger: 'blur'
          },
          {
            validator: isPrice,
            trigger: 'blur'
          }
        ]
      },
      rules3: {
        name: [
          {
            required: true,
            message: '请输入邮箱',
            trigger: 'blur'
          },
          {
            validator: youxiang,
            trigger: 'blur'
          }
        ],
        region: [
          {
            required: true,
            message: '请输入手机号',
            trigger: 'blur'
          },
          {
            validator: shouji,
            trigger: 'blur'
          }
        ],
        desc: [
          {
            required: true,
            message: '请输入身份证',
            trigger: 'blur'
          },
          {
            validator: sfz,
            trigger: 'blur'
          }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('成功!');
          this.$refs[formName].resetFields();
        } else {
          console.log('失败!!');
          return false;
        }
      });
    }
  }
};
</script>

